@use 'base/variables' as v;
@use 'base/mixins' as m;

.resume {

    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;

    @include m.tablet {
        align-items: flex-start;
    }

    &__gridEdEx {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 2rem;
        }
    }

    &__titulo {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: 1rem;

        @include m.tablet {
            align-items: center;
            justify-content: none;
        }
    }

    &__subTitulo {
        font-size: 1rem;

        @include m.tablet {
            font-size: 2rem;

        }

        font-weight: 500;
        font-family: v.$fuentePrincipal;
        color: #343a40;
    }

    & .material-symbols-outlined {
        font-size: 2rem;
        color: v.$colorBtn;
        font-variation-settings:
            'FILL' 0,
            'wght' 500,
            'GRAD' 0,
            'opsz' 48
    }

    &__seccionEdEx {}

    &__boxEdEx {
        display: grid;
        grid-template-columns: 2.4rem 1fr;
    }

    &__seccionEdEx {
        position: relative;

        list-style-type: none;

        &::before {
            position: absolute;
            left: 0;
            content: "";
            width: 1rem;
            height: 1rem;
            border-radius: 100%;
            border: 3px solid v.$colorBtn;
        }
    }

    &__boxEdEx {
        position: relative;

        &::before {
            position: absolute;
            top: 1.3rem;
            left: .6rem;
            content: "";
            width: 2px;
            height: calc(100% - 1.3rem);
            background-color: v.$colorParrafoCuatro;
        }
    }

    &__experienciaTexto {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 1rem;
        padding-bottom: 3rem;
    }

    &__tituloEdEx {
        color: v.$colorBtn;
    }

    &__seccionBoxGeneral {
        min-width: 100%;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }

    &__seccionBarras {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(2, 1fr);

        }
    }

    &__seccionBoxBarras {
        min-width: 100%;
    }

    &__gridBoxParrafos {
        display: grid;
        grid-template-columns: 1fr auto;
        margin-bottom: .4rem;

        & .resume__parrafo {
            font-size: 900;
        }
    }
}