@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-image: linear-gradient(rgba(255, 255, 255, .7), rgba(255, 255, 255, .7)), url("https://retrina.com/demo/nill/assets/img/nill.jpg");
    background-repeat: no-repeat;
    background-position: 0 center;
    background-size: 150% 100%;

    &__grid {
        margin-top: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 2rem;
    }
}