.barra {
  display: grid;
  grid-template-columns: 1fr 1px;
  align-items: center;
  padding: 0;
  border-radius: 4px;
  background-color: #e6e6e6;
}
.barra.temaClaro {
  background-color: #902913;
}
.barra :root {
  --habilidades-bar-lenght: 0%;
}
.barra__bar, .barra__bar::after {
  position: relative;
  background-color: #0F1115;
  width: 100%;
  height: 3px;
  overflow: hidden;
}
.barra__bar.activo::after {
  content: "";
  position: absolute;
  left: -100%;
  background-color: #E24727;
  transition: 0.3s ease-in-out;
  animation: habilidades-barra 1.5s ease-in-out forwards;
}
.barra__bar--0::after {
  --habilidades-bar-lenght: 0%;
}
.barra__bar--5::after {
  --habilidades-bar-lenght: 5%;
}
.barra__bar--10::after {
  --habilidades-bar-lenght: 10%;
}
.barra__bar--15::after {
  --habilidades-bar-lenght: 15%;
}
.barra__bar--20::after {
  --habilidades-bar-lenght: 20%;
}
.barra__bar--25::after {
  --habilidades-bar-lenght: 25%;
}
.barra__bar--30::after {
  --habilidades-bar-lenght: 30%;
}
.barra__bar--35::after {
  --habilidades-bar-lenght: 35%;
}
.barra__bar--40::after {
  --habilidades-bar-lenght: 40%;
}
.barra__bar--45::after {
  --habilidades-bar-lenght: 45%;
}
.barra__bar--50::after {
  --habilidades-bar-lenght: 50%;
}
.barra__bar--55::after {
  --habilidades-bar-lenght: 55%;
}
.barra__bar--60::after {
  --habilidades-bar-lenght: 60%;
}
.barra__bar--65::after {
  --habilidades-bar-lenght: 65%;
}
.barra__bar--70::after {
  --habilidades-bar-lenght: 70%;
}
.barra__bar--75::after {
  --habilidades-bar-lenght: 75%;
}
.barra__bar--80::after {
  --habilidades-bar-lenght: 80%;
}
.barra__bar--85::after {
  --habilidades-bar-lenght: 85%;
}
.barra__bar--90::after {
  --habilidades-bar-lenght: 90%;
}
.barra__bar--95::after {
  --habilidades-bar-lenght: 95%;
}
.barra__bar--100::after {
  --habilidades-bar-lenght: 100%;
}

@keyframes habilidades-barra {
  to {
    transform: translateX(var(--habilidades-bar-lenght));
  }
}
