@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    position: fixed;
    width: 100%;
    z-index: 2;

    &__grid {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 1rem;
        justify-content: space-between;
        margin: 1rem;

        background-color: transparent;
    }
}