@use 'base/variables' as v;
@use 'base/mixins' as m;

.portfolio {

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;

    @include m.tablet {
        align-items: flex-start;
    }

    & .material-symbols-outlined {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 1rem;
        height: 1rem;
        font-size: 1rem;

        @include m.tablet {
            font-size: 1.4rem;
            width: 2rem;
            height: 2rem;
        }

        border-radius: 4px;
        transition: background-color .3s ease-in-out;

        &:hover {
            background-color: v.$colorBtn;
            color: v.$colorFondoDiv;
            cursor: pointer;
        }
    }

    &__ImagenesGrid {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: 2rem;

        @include m.tablet {
            grid-template-columns: repeat(3, 1fr);
        }
    }



    &__imagenesaBox {
        position: relative;
        overflow: hidden;
        height: min-content;

        &::after {
            transition: opacity .3s ease-in-out;
            content: "";
            position: absolute;
            top: 1rem;
            left: 1rem;
            width: 40%;
            height: 33%;
            border: 2px solid rgba(0, 0, 0, 0);
            border-right: none;
            border-bottom: none;
            background-color: transparent;
            z-index: 1;
            opacity: 0;
        }

        &::before {
            transition: opacity .3s ease-in-out;
            content: "";
            position: absolute;
            bottom: 1rem;
            right: 1rem;
            width: 40%;
            height: 33%;
            border: 2px solid rgba(0, 0, 0, 0);
            border-top: none;
            border-left: none;
            background-color: transparent;
            z-index: 1;
            opacity: 0;
        }

        & .portfolio__boxCirCol {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            padding: 1rem;
            border-radius: 100%;
            opacity: .15;
        }
    }

    &__boxFlex {
        background-color: rgba(255, 255, 255, .5);
        border-radius: 12px;
        transition: opacity .3s ease-in-out;
        gap: 1rem;
        opacity: 1;
        position: absolute;
        top: 25%;
        bottom: 30%;
        left: 30%;
        right: 25%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        z-index: 2;
        opacity: 0;
    }

    &__boxGrid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
    }

    &__imagen {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        transition-property: transform, filter;
        transition-duration: .3s;
        transition-timing-function: ease-in-out;
        filter: grayscale(0%);

        &.activo {
            filter: grayscale(100%);
        }
    }

    &__H4 {
        color: v.$colorTitulo;
        padding: .2rem;
    }

    & .material-symbols-outlined {
        color: v.$colorParrafo;
        background-color: v.$colorFondoDiv;
        padding: .4rem;
        border-radius: 3px;
    }

    &__imagenesaBox.activo {
        overflow: hidden;
        position: relative;

        &::after {

            transition: opacity .3s ease-in-out;
            border-color: v.$colorFondoDiv;
            opacity: 1;
        }

        &::before {

            transition: opacity .3s ease-in-out;
            border-color: v.$colorFondoDiv;
            opacity: 1;
        }

        & .portfolio__boxCirCol {
            background-color: black;
            border-color: v.$colorBtn;
            border-radius: 0;
            transition: border-Radius .3s ease-in-out;
        }

        & .portfolio__boxFlex {
            opacity: 1;
        }
    }

    &__navBtn {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: .2rem;

        @include m.tablet {
            gap: 1rem;

        }
    }
}