@use 'base/variables' as v;
@use 'base/mixins' as m;

.services {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;

    @include m.tablet {
        align-items: flex-start;

    }

    &__grid {
        margin-top: 4rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        row-gap: 4.4rem;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
        }
    }

    &__box {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        gap: .4rem;

        & .material-symbols-outlined {
            transition: transform .3s ease-in-out;
            font-size: 2.8rem;
            color: v.$colorBtn;
            font-variation-settings:
                'FILL' 0,
                'wght' 300,
                'GRAD' 0,
                'opsz' 48
        }
    }

    &__box.activo {
        & .material-symbols-outlined {
            transition: transform .3s ease-in-out;
            transform: scaleX(-1);
        }
    }

    &__flexInfo {
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }

    &__subTitulo {
        margin: 0;
        font-size: 1.2rem;
        font-family: v.$fuentePrincipal;
        color: v.$colorParrafo;
    }
}