@use 'base/variables' as v;
@use 'base/mixins' as m;

.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;

    @include m.tablet {
        align-items: flex-start;
        gap: 1.4rem;
    }


    &__grid {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 1rem;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 2rem;
        }
    }

    &__box {
        padding: 1rem;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        gap: 1.4rem;
    }
}