@use 'base/variables' as v;
@use 'base/mixins' as m;

.inicio {
    background-image: linear-gradient(rgba(255, 255, 255, .3), rgba(255, 255, 255, .3)), url("https://retrina.com/demo/nill/assets/img/slider-1.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 100%;
    animation: imagenZoom 10s 1s infinite alternate-reverse;


    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;

    &__imagen {
        width: 10rem;
        border-radius: 100%;
        border: 4px solid white;

        @include m.tablet {
            width: 20rem;
        }
    }

    &__ocupacionBox {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: .4rem;

        padding-right: 4px;
        border-right: 4px solid v.$colorParrafo;
        overflow-x: hidden;
        white-space: nowrap;

        &.part1 {
            animation: borderRight .5s infinite forwards;
        }

        &.part2 {
            animation: parrafoWidthUno 2s steps(14) infinite alternate;
        }
    }

    &__parrafoCapitalize {
        text-transform: capitalize;
        font-size: 1.8rem;
    }

    &__parrafoOcupacion {
        font-size: 1.8rem;
        color: v.$colorParrafo;
    }
}

@keyframes imagenZoom {
    to {
        background-size: 100% 100%;
    }

    from {
        background-size: 200% 200%;
    }
}

@keyframes borderRight {
    to {
        border-color: v.$colorParrafo;
    }

    from {
        border-color: transparent;
    }
}

@keyframes parrafoWidthUno {
    0% {
        width: 15rem;
    }

    50% {
        width: 7rem;
    }

    100% {
        width: 12.2rem;
    }
}