@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

    @include m.tablet {
        display: block;
    }

    &__hidden {
        overflow: hidden;
    }

    &__boxTestimonioGeneral {
        margin: v.$separacion 0;
        width: 200%;
        display: flex;
        flex-direction: column;

        @include m.tablet {
            flex-direction: row;

        }

        align-items: center;
        position: relative;

        transition: right .4s ease-in-out;

        &.right0 {
            right: 0%;
        }

        &.right100 {
            right: 100%;
        }
    }

    &__seccionTestimonio {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
        position: relative;
        width: 100%;
    }

    &__boxTestimonio {
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        gap: 1rem;

        @include m.tablet {

            align-items: flex-start;
            gap: 4rem;
        }

        // width: 90%;
        padding: 5%;
    }

    &__imagen {
        border-radius: 100%;
        border: 2px solid v.$colorBtn;
        width: 6rem;
    }

    &__perfil {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            flex-direction: row;

        }

        align-items: center;
        gap: 2rem;
    }

    &__parrafoNombre {
        color: v.$colorParrafoDos;
        font-weight: 900;
        margin-left: 0;
        text-align: center;

        @include m.tablet {
            text-align: start;
            margin-left: .4rem;
        }

        &:before {
            content: "-";
            margin-right: .4rem;
        }
    }


    &__navSelectorArticle {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        gap: .4rem;
        width: 10%;
        margin: 0 45%;
    }

    &__navSelectorArticleBtn {
        background-color: darken(v.$colorBtnDos, 6);
        width: 2rem;
        height: .4rem;
        border: none;
        transition: width .3s ease-in-out;

        &.activo {
            background-color: v.$colorBtn;
            width: 3.5rem;
        }

        &:hover {
            cursor: pointer;
        }
    }
}