@use 'base/variables' as v;
@use 'base/mixins' as m;

.about {
    margin: 0;

    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3rem;

        @include m.tabletXL {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 2rem;
        }
    }

    &__boxImagen {
        border-radius: 3px;
        border: 2px solid v.$colorBtnDos;
        width: 30rem;
        padding: 4px;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &__imagen {
        width: 100%;
    }

    &__articulo {
        display: flex;
        flex-direction: column;

        align-items: center;
        gap: 2rem;

        @include m.tablet {
            align-items: flex-start;
        }
    }

    &__parrafoSubtitulo {
        font-size: 1.3rem;
        font-weight: 500;
    }

    &__navBtn {
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
        gap: 1rem;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(2, 1fr);

        }
    }

    &__stats {
        display: grid;
        grid-column: 1/3;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;

        @include m.tablet {
            grid-template-columns: repeat(4, 1fr);
        }
    }

    &__statsDetalle {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: .4rem;

        & .material-symbols-outlined {
            font-size: 3rem;
            font-variation-settings:
                'FILL' 0,
                'wght' 300,
                'GRAD' 0,
                'opsz' 48
        }
    }

    &__parrafoTres {
        font-size: 1.8rem;
        font-weight: 500;
    }

    &__parrafoCuatro {
        font-size: 1.1rem;
        font-weight: 400;
    }
}