@use 'base/variables' as v;
@use 'base/mixins' as m;

.myWork {
    background-color: rgba(0, 0, 0, .4);
    position: fixed;
    // display: none;
    min-width: 100%;
    max-width: 100%;
    min-height: 981px;
    max-height: 981px;
    z-index: 2;
    overflow-y: hidden;
    opacity: 0;
    transform: scale(0);

    &.activo {
        transition: transform .2s ease-in-out, opacity .1s ease-in-out;
        transform: scale(1);
        opacity: 1;
    }

    &__grid {
        max-height: 881px;
        margin: 50px 5.5%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3rem;
        background-color: white;
        border-radius: 6px;
    }

    &__header {
        width: 96%;
        padding: 2% 2%;
        border-bottom: 1px solid v.$colorBorder;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        gap: 1rem;

        & .material-symbols-outlined {
            font-size: 2rem;
            border: 3px solid transparent;
            transition: color .1s ease-in-out;

            &:hover {
                cursor: pointer;
            }

            &:active {
                color: v.$colorBtn;
            }
        }

        & .myWork__H5 {
            font-family: v.$fuentePrincipal;
            font-size: 1.4rem;
            font-weight: 500;
        }
    }

    &__footer {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        max-width: 85%;
        padding: 2% 15% 10% 15%;
        overflow-y: scroll;

        & .myWork__H5 {
            font-size: 1.6rem;
            font-weight: 600;
        }
    }

    &__parrafo {
        text-align: center;

        @include m.tablet {
            width: 70%;
            margin: 0 15%;
        }
    }

    &__secDetgrid {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 1rem;
    }

    &__artDetgrid {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: .4rem;

        & .myWork__parrafo {
            width: max-content;
            margin: 0;
        }

        & .myWork__parrafoDos {
            font-size: 1.2rem;
        }
    }

    &__imagen {
        width: 90%;
        border-radius: 4px;
    }

    &__parrafoTres {
        font-size: 1.6rem;
    }
}