@use 'base/variables' as v;
@use 'base/mixins' as m;

body {
    background-color: v.$colorFondoBody;
    user-select: none;
}

#cuerpo.cuerpo {
    overflow: hidden;
    max-width: 100%;
}

.inicio,
.about,
.resume,
.services,
.portfolio,
.testimonials,
.blog,
.contact,
.footer {
    position: relative;
    padding: 2rem .8rem;

    @include m.tablet {
        padding: v.$separacion;
    }

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

#inicioSeccion {
    justify-content: center;
}

p {
    font-family: v.$fuenteParrafo;
    margin-top: 0;
    margin-bottom: 0;
}

a {
    text-decoration: none;
}

[class*="__H1"] {
    font-family: v.$fuenteTitulo;
    font-size: 1.6rem;
    font-weight: 400;
    padding: 0;
    margin: 0 auto;
}

[class*="__H2"] {
    font-family: v.$fuenteTitulo;
    font-size: 1.2rem;

    @include m.tablet {
        font-size: 1.8rem;
    }

    font-weight: 600;
    line-height: 1.3;
    padding: 0;
    margin: 0;
    color: v.$colorParrafoDos;
}

[class*="__H3"] {

    font-family: v.$fuenteTitulo;
    font-size: 1rem;
    font-weight: 500;
    padding: 0;
    margin: 0;
    transform: rotate(-90deg);
    width: 2.8rem;
    z-index: 0;

    position: absolute;
    height: 100%;
    // top: 20rem;
    left: 0;

    display: none;

    @include m.tablet {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    & .h3Rotacion {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        text-transform: uppercase;
        color: v.$colorParrafoTres;
        white-space: nowrap;

        &::after {
            position: absolute;
            content: "";
            background-color: v.$colorBtn;
            width: 3rem;
            height: .04rem;
            right: 8rem;
        }

        &::before {
            position: absolute;
            content: "";
            background-color: v.$colorBtn;
            width: 3rem;
            height: .04rem;
            left: 8rem;
        }
    }
}

[class*="__H4"] {
    font-family: v.$fuenteTituloProfesion;
    font-size: 1rem;

    @include m.tablet {
        font-size: 1.4rem;
    }

    font-weight: 700;
    line-height: 1.5;
    padding: 0;
    margin: 0;
}

[class*="__H5"] {
    font-family: v.$fuenteTituloProfesion;
    font-weight: 600;
    line-height: 1.5;
    margin: 0;
    padding: 0;
}

[class*="__H6"] {
    font-family: v.$fuenteTitulo;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.5;
    margin: 0;
    padding: 0;
    text-align: center;
}

[class*="__parrafo"] {
    font-family: v.$fuenteParrafo;
    text-align: justify;

    @include m.tablet {
        text-align: start;
    }

    color: v.$colorParrafoCuatro;
    font-size: 1rem;
    font-weight: 600;
    line-height: 1.5;
    padding: 0;
}

[class*="__parrafoDos"] {
    font-family: v.$fuentePrincipal;
    color: v.$colorParrafo;
    font-size: 1rem;
    font-weight: 600;
    line-height: 1.5;
    padding: 0;
}

[class*="__spanProfesion"] {
    font-family: v.$fuenteTituloProfesion;
    color: v.$colorParrafoTres;
    font-size: 1.4rem;
    font-weight: 500;
    line-height: 1.5;
    padding: 0;

    @include m.tablet {
        font-size: 1.8rem;
    }
}



[class*="__tel"] {
    font-family: v.$fuenteTitulo;
    color: v.$colorTitulo;
    font-size: 1.2rem;
    font-weight: 700;
    line-height: 1.5;
    padding: 0;
}

[class*="__ParrafoTres"] {
    color: v.$colorParrafoTres;
    font-family: v.$fuenteParrafo;
    line-height: 1.6;
    font-size: 1.1rem;
}

[class*="__ComilladoBoxFlex"] {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: .6rem;
}

[class*="__spanComilla"] {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 4.6rem;
    color: v.$colorBtn;
}

[class*="__parrafoCuatro"] {
    font-size: 1.1rem;
    font-weight: 400;
}

[class*="__parrafoCinco"] {
    color: v.$colorBtn;
    font-family: v.$fuenteParrafo;
    line-height: 1.6;
    font-size: 1.1rem;
    text-align: justify;
    position: relative;
}

.colorUno {
    color: v.$colorBtn;
}

.btnGeneralUno,
.btnGeneralDos,
.btnGeneralTres {
    width: 100%;
    padding: 0.4rem 1rem;
    border: 1px solid transparent;
    font-size: 1rem;
    font-weight: 900;
    font-family: v.$fuentePrincipal;
    border-radius: 2rem;

    @include m.tablet {
        width: max-content;
        font-size: 1.4rem;
        padding: 0.6rem 1.6rem;
    }

    &:hover {
        cursor: pointer;
    }
}

.btnGeneralUno {
    background-color: v.$colorBtn;
    color: v.$colorTextoBtn;

    &:hover {
        background-color: darken(v.$colorBtn, 4);
    }
}

.btnGeneralDos {
    background-color: v.$colorFondoDiv;
    color: v.$colorTextoBtnDos;
    border-color: v.$colorBtn;
}

.btnGeneralTres {
    width: max-content;
    background-color: v.$colorFondoDiv;
    color: v.$colorTitulo;
    transition-property: background-color, color;
    transition-duration: .3s;
    transition-timing-function: ease-in-out;

    &:hover {
        background-color: lighten(v.$colorBtn, 10);
        color: v.$colorFondoDiv;
    }
}

.btnGeneralCuatro {
    margin: 0;
    padding: 0;
    border: none;
    background-color: transparent;
    font-size: 1.4rem;
    font-family: v.$fuentePrincipal;
    color: v.$colorBtn;

    &:hover {
        cursor: pointer;
    }
}

.btnNavOpc {
    background-color: transparent;
    border: none;
    font-size: 1.6rem;
    font-family: v.$fuenteParrafo;
    color: v.$colorParrafoDos;
    font-weight: 700;
}

.btnPortfolio {
    padding: .4rem 1rem;
}

.navegadorRedes {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    gap: 1.6rem;
    align-items: center;
    align-items: center;
    width: min-content;

    &__links {
        background-color: v.$colorBtn;
        padding: .8rem;
        border-radius: 100%;
        color: v.$colorTextoBtn;
        font-size: 1.6rem;
        transition: box-shadow .4s ease-in-out;

        &:hover {
            box-shadow: 0 0 30px v.$colorBtn;
        }

    }
}


.barra {
    display: grid;
    grid-template-columns: 1fr 1px;
    align-items: center;

    // margin: .7rem 0;
    padding: 0;
    border-radius: 4px;
    background-color: lighten(v.$grisClaro, 10);

    &.temaClaro {
        background-color: darken(v.$colorBtn, 20);
    }

    :root {
        --habilidades-bar-lenght: 0%;
    }

    &__bar,
    &__bar::after {
        position: relative;
        background-color: v.$colorFondoOscuro;
        width: 100%;
        height: 3px;
        // border-radius: 8px;
        overflow: hidden;
    }

    &__bar.activo {
        &::after {
            content: "";
            position: absolute;
            left: -100%;
            background-color: v.$colorBtn;
            transition: 0.3s ease-in-out;
            animation: habilidades-barra 1.5s ease-in-out forwards;
        }
    }

    &__bar--0::after {
        --habilidades-bar-lenght: 0%;
    }

    &__bar--5::after {
        --habilidades-bar-lenght: 5%;
    }

    &__bar--10::after {
        --habilidades-bar-lenght: 10%;
    }

    &__bar--15::after {
        --habilidades-bar-lenght: 15%;
    }

    &__bar--20::after {
        --habilidades-bar-lenght: 20%;
    }

    &__bar--25::after {
        --habilidades-bar-lenght: 25%;
    }

    &__bar--30::after {
        --habilidades-bar-lenght: 30%;
    }

    &__bar--35::after {
        --habilidades-bar-lenght: 35%;
    }

    &__bar--40::after {
        --habilidades-bar-lenght: 40%;
    }

    &__bar--45::after {
        --habilidades-bar-lenght: 45%;
    }

    &__bar--50::after {
        --habilidades-bar-lenght: 50%;
    }

    &__bar--55::after {
        --habilidades-bar-lenght: 55%;
    }

    &__bar--60::after {
        --habilidades-bar-lenght: 60%;
    }

    &__bar--65::after {
        --habilidades-bar-lenght: 65%;
    }

    &__bar--70::after {
        --habilidades-bar-lenght: 70%;
    }

    &__bar--75::after {
        --habilidades-bar-lenght: 75%;
    }

    &__bar--80::after {
        --habilidades-bar-lenght: 80%;
    }

    &__bar--85::after {
        --habilidades-bar-lenght: 85%;
    }

    &__bar--90::after {
        --habilidades-bar-lenght: 90%;
    }

    &__bar--95::after {
        --habilidades-bar-lenght: 95%;
    }

    &__bar--100::after {
        --habilidades-bar-lenght: 100%;
    }
}

@keyframes habilidades-barra {
    to {
        transform: translateX(var(--habilidades-bar-lenght))
    }
}


.material-symbols-outlined {
    font-variation-settings:
        "FILL" 0,
        "wght" 500,
        "GRAD" 0,
        "opsz" 48;
}


.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    margin-top: 0rem;
    width: 100%;

    &__Input {
        box-shadow: none;
        background-color: unset;
        height: 2rem;
        width: 90%;
        min-width: 90%;
        max-width: 90%;
        padding: .3rem .7rem;
        border: none;
        border-bottom: 2px solid v.$colorBorder;
        border-radius: 2px;
        background-color: v.$colorFondoBody;
        transition: background-color .2s ease-in-out, border-color .2s ease-in-out;
        outline: none;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-size: 1.2rem;
        font-weight: 100;

        &:focus {
            border-color: v.$colorBtn;
        }



    }

    textarea {
        height: 10rem;
        min-height: 10rem;
        max-height: 20rem;
    }
}

.redes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.4rem;

    &__boxRedSocial {
        color: v.$colorParrafo;
    }

    & span {
        font-size: 1.6rem;
    }
}

.colorDos {
    color: v.$colorParrafoTres;
}

.aCopy {
    color: v.$colorParrafoCuatro;
}

.Copyright {
    text-align: center;
}

.colorParrafoDos {
    color: v.$colorParrafoDos;
    font-family: v.$fuenteParrafo;
}



.info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;

    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    &__parrafoDos {
        display: flex;
        flex-direction: column;


        font-size: 1rem;
        font-weight: 400;
        white-space: nowrap;

        & span,
        & a {
            font-weight: 900;
            color: v.$colorParrafoTres;
        }
    }
}

.headerSeccion,
.inicioSeccion,
.about__grid,
.resume__gridEdEx,
.services__grid,
.portfolio__ImagenesGrid,
.testimonials__hidden,
.blog__grid,
.contact__grid,
.footer__grid {
    z-index: 1;
}