@use 'base/variables' as v;
@use 'base/mixins' as m;

.readMore {
    background-color: rgba(0, 0, 0, .4);
    position: fixed;
    max-width: 100%;
    max-height: 981px;
    z-index: 2;
    overflow-y: hidden;
    transform: scale(0);

    &.activo {
        transition: transform .2s ease-in-out;
        transform: scale(1);
    }

    &__grid {
        max-height: 881px;
        margin: 50px 12.5%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3rem;
        background-color: white;
        border-radius: 6px;
    }

    &__header {
        width: 96%;
        padding: 2% 2%;
        border-bottom: 1px solid v.$colorBorder;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        gap: 1rem;

        & .material-symbols-outlined {
            font-size: 2rem;
            border: 3px solid transparent;
            transition: color .1s ease-in-out;

            &:hover {
                cursor: pointer;
            }

            &:active {
                color: v.$colorBtn;
            }
        }

        & .readMore__H5 {
            font-family: v.$fuentePrincipal;
            font-size: 1.4rem;
            font-weight: 500;
        }
    }

    &__footer {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 2rem;
        max-width: 75%;
        padding: 2% 12.5% 10% 12.5%;
        overflow-y: scroll;

        & .readMore__H5 {
            font-size: 1.6rem;
            font-weight: 600;
        }
    }

    &__imagen {
        width: 90%;
        border-radius: 4px;
    }

    &__parrafoTres {
        font-size: 1.6rem;
    }
}